import { Directive, ElementRef } from '@angular/core';
import { CommonService } from './../services/common/common.service'
@Directive({
  selector: '[appTranslate]'
})
export class TranslateDirective{

  text:string[][] = [
    //sales
    ['Sales', ''],
    ['head office list', ' '],
    ['member list', ''],
    //calculate
    ['Calculate', ''],
    ['calculate manage', ''],
    ['user profit', ''],
    //user
    ['User', ''],
    ['connecting user', ' '],
    ['black list', ''],
    ['IP inquire', 'IP'],
    //deposit and withdraw
    ['Deposit &amp; Withdraw', ''],
    ['deposit management', ' '],
    ['withdraw management', ' '],
    //notice
    ['Notice', ''],
    ['lobby notice', 'web '],
    ['in gaming table', ''],
    ['1:1 support', '11'],

    ['Log', ''],
    ['game log', ''],
    ['jackpot log', ''],
    ['transfer log', ''],
    ['point log', ''],
  ]

  constructor(private el: ElementRef, private commonSrvc:CommonService) {}

   ngAfterViewInit(){
    this.translate()

   }

   toKorean(){
    for(let t of this.text ){
      if(t[0] == this.el.nativeElement.innerHTML){
        this.el.nativeElement.innerHTML = t[1]
      }
    }
   }

   toEnglish(){
    for(let t of this.text ){
      if(t[1] == this.el.nativeElement.innerHTML){
        this.el.nativeElement.innerHTML = t[0]
      }
    }
   }

   translate(){
      if(localStorage.getItem('language') == 'korean'){
        this.toKorean()
      }else if(localStorage.getItem('language') == 'english'){
        this.toEnglish()
      }else{
        this.toKorean()
      }
   }
}
